webpackJsonp([134464351371100],{

/***/ 831:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"victorybrushline\"><a href=\"#victorybrushline\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>VictoryBrushLine</h1>\n<p><code class=\"language-text\">VictoryBrushLine</code> renders a brush component centered around a line. It may be used in place of the default <code class=\"language-text\">axisComponent</code> or <code class=\"language-text\">gridComponent</code> within <code class=\"language-text\">VictoryAxis</code>. Use <code class=\"language-text\">VictoryBrushLine</code> instead of <a href=\"https://formidable.com/open-source/victory/docs/victory-brush-container\"><code class=\"language-text\">VictoryBrushContainer</code></a> in charts that require multiple brushes.</p>\n<pre class=\"pre\"><code class=\"language-playground\">&lt;VictoryChart domainPadding={50}&gt;\n  &lt;VictoryScatter\n    size={4}\n    style={{ data: { fill: &quot;tomato&quot; }}}\n    data={[\n      { x: 1, y: 1 }, { x: 2, y: 2 }, { x: 3, y: 4 }\n    ]}\n  /&gt;\n  &lt;VictoryAxis\n    tickValues={[1, 2, 3]}\n    gridComponent={&lt;VictoryBrushLine width={30}/&gt;}\n  /&gt;\n&lt;/VictoryChart&gt;</code></pre>\n<h2 id=\"props\"><a href=\"#props\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Props</h2>\n<h3 id=\"allowdrag\"><a href=\"#allowdrag\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>allowDrag</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>The optional <code class=\"language-text\">allowDrag</code> prop accepts a boolean that enables dragging behavior for the highlighted brush area. Resizing will still be enabled when the <code class=\"language-text\">allowDrag</code> prop is set to false.</p>\n<p><em>default:</em> <code class=\"language-text\">allowDrag={true}</code></p>\n<h3 id=\"allowresize\"><a href=\"#allowresize\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>allowResize</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>The optional <code class=\"language-text\">allowResize</code> prop accepts a boolean that enables resizing the highlighted brush area. Dragging will still be enabled when the <code class=\"language-text\">allowResize</code> prop is set to false, but the dimensions of the brush area will be fixed.</p>\n<p><em>default:</em> <code class=\"language-text\">allowResize={true}</code></p>\n<h3 id=\"brushareacomponent\"><a href=\"#brushareacomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushAreaComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">brushAreaComponent</code> prop specifies the component to be rendered for the interactive brush region.\nThis component will be supplied with the following props: x, y, width, height, and style.\nWhen this prop is not specified, a <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#box\"><code class=\"language-text\">Box</code></a> component will be rendered.</p>\n<p><em>default:</em> <code class=\"language-text\">brushAreaComponent={&lt;Box/&gt;}</code></p>\n<h3 id=\"brushareastyle\"><a href=\"#brushareastyle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushAreaStyle</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">brushAreaStyle</code> prop adds custom styles to the <code class=\"language-text\">brushAreaComponent</code>. This prop should be given as\nan object of SVG style attributes. Styles supplied to <code class=\"language-text\">brushAreaStyle</code> are assigned to the following default styles:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  stroke<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span><span class=\"token punctuation\">,</span>\n  fill<span class=\"token punctuation\">:</span> <span class=\"token string\">\"black\"</span><span class=\"token punctuation\">,</span>\n  opacity<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span>d<span class=\"token punctuation\">,</span> a<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> a <span class=\"token operator\">?</span> <span class=\"token number\">0.2</span> <span class=\"token punctuation\">:</span> <span class=\"token number\">0.1</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p><strong>Note:</strong> <code class=\"language-text\">cursor</code> styles should not be applied via this prop, as they are dynamically assigned</p>\n<h3 id=\"brushareawidth\"><a href=\"#brushareawidth\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushAreaWidth</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">brushAreaWidth</code> prop is used to specify the width of the interactive brush region. If this prop is not supplied, the <code class=\"language-text\">width</code> prop will be used.</p>\n<h3 id=\"brushcomponent\"><a href=\"#brushcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">brushComponent</code> prop specifies the component to be rendered for active brush.\nThis component will be supplied with the following props: x, y, width, height, and style.\nWhen this prop is not specified, a <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#box\"><code class=\"language-text\">Box</code></a> component will be rendered.</p>\n<p><em>default:</em> <code class=\"language-text\">brushComponent={&lt;Box/&gt;}</code></p>\n<h3 id=\"brushdomain\"><a href=\"#brushdomain\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushDomain</h3>\n<p><code class=\"language-text\">type:  array[low, high]</code></p>\n<p>The optional <code class=\"language-text\">brushDomain</code> prop describes the highlighted state. This prop should be given as an array of the minimum and maximum values of the highlighted region.</p>\n<p><em>example:</em> <code class=\"language-text\">brushDomain={[50, 100]}</code></p>\n<h3 id=\"brushstyle\"><a href=\"#brushstyle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushStyle</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">brushStyle</code> prop adds custom styles to the <code class=\"language-text\">brushComponent</code>. This prop should be given as\nan object of SVG style attributes. Styles supplied to <code class=\"language-text\">brushStyle</code> are assigned to the following default styles:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  pointerEvents<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span><span class=\"token punctuation\">,</span>\n  stroke<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span><span class=\"token punctuation\">,</span>\n  fill<span class=\"token punctuation\">:</span> <span class=\"token string\">\"black\"</span><span class=\"token punctuation\">,</span>\n  opacity<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span>d<span class=\"token punctuation\">,</span> a<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> a <span class=\"token operator\">?</span> <span class=\"token number\">0.4</span> <span class=\"token punctuation\">:</span> <span class=\"token number\">0.3</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"brushwidth\"><a href=\"#brushwidth\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>brushWidth</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">brushWidth</code> prop is used to specify the width of the active brush. If this prop is not supplied, the <code class=\"language-text\">width</code> prop will be used.</p>\n<h3 id=\"classname\"><a href=\"#classname\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>className</h3>\n<p><code class=\"language-text\">type: string</code></p>\n<p>This prop specifies the class name that will be applied to the rendered element</p>\n<h3 id=\"dimension\"><a href=\"#dimension\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>dimension</h3>\n<p><code class=\"language-text\">type: &quot;x&quot; || &quot;y&quot;</code></p>\n<p>The <code class=\"language-text\">dimension</code> prop specified whether the brush will be vertical (“y”), or horizontal (“x”)</p>\n<h3 id=\"disable\"><a href=\"#disable\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>disable</h3>\n<p><code class=\"language-text\">type: boolean</code></p>\n<p>When the <code class=\"language-text\">disable</code> prop is set to <code class=\"language-text\">true</code>, <code class=\"language-text\">VictoryBrushLine</code> events will not fire.</p>\n<h3 id=\"events\"><a href=\"#events\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>events</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">events</code> prop specifies a set of events that will be attached to the brush component group. This prop should not be set manually.</p>\n<h3 id=\"groupcomponent\"><a href=\"#groupcomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>groupComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>This prop specifies the element used to group rendered elements</p>\n<p><em>default:</em> <code class=\"language-text\">&lt;g/&gt;</code></p>\n<h3 id=\"handlecomponent\"><a href=\"#handlecomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>handleComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">handleComponent</code> prop specifies the component to be rendered for each handle.\nThis component will be supplied with the following props: x, y, width, height, and style.\nWhen this prop is not specified, a <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#box\"><code class=\"language-text\">Box</code></a> component will be rendered.</p>\n<p><em>default:</em> <code class=\"language-text\">handleComponent={&lt;Box/&gt;}</code></p>\n<h3 id=\"handlestyle\"><a href=\"#handlestyle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>handleStyle</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">handleStyle</code> props adds custom styles to the <code class=\"language-text\">handleComponent</code>. This prop should be given as\nan object of SVG style attributes. Styles supplied to <code class=\"language-text\">handleStyle</code> are assigned to the following default styles:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  pointerEvents<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span><span class=\"token punctuation\">,</span>\n  stroke<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span><span class=\"token punctuation\">,</span>\n  fill<span class=\"token punctuation\">:</span> <span class=\"token string\">\"none\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h3 id=\"handlewidth\"><a href=\"#handlewidth\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>handleWidth</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">handleWidth</code> prop is used to specify the width of each handle component.</p>\n<p><em>default:</em> <code class=\"language-text\">handleWidth={10}</code></p>\n<h3 id=\"linecomponent\"><a href=\"#linecomponent\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>lineComponent</h3>\n<p><code class=\"language-text\">type: element</code></p>\n<p>The <code class=\"language-text\">lineComponent</code> prop specifies the component to render for the underlying axis or grid line.\nThis component will be supplied with the following props: x1, y1, x2, y2 and style.\nWhen this prop is not specified, an <a href=\"https://formidable.com/open-source/victory/docs/victory-primitives#axis\"><code class=\"language-text\">Axis</code></a> component will be rendered.</p>\n<p><em>default:</em> <code class=\"language-text\">lineComponent={&lt;Axis/&gt;}</code></p>\n<h3 id=\"onbrushdomainchange\"><a href=\"#onbrushdomainchange\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>onBrushDomainChange</h3>\n<p><code class=\"language-text\">type: function</code></p>\n<p>The <code class=\"language-text\">onBrushDomainChange</code> prop specifies a callback function which will be called whenever the brush domain changes. The callback provided will be called with the following arguments:</p>\n<ul>\n<li><code class=\"language-text\">currentDomain</code>: The current brush domain</li>\n<li><code class=\"language-text\">props</code>: the current set of props for <code class=\"language-text\">VictoryBrushLine</code></li>\n</ul>\n<h3 id=\"scale\"><a href=\"#scale\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>scale</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>This prop specifies <code class=\"language-text\">scale</code> of the parent chart with <code class=\"language-text\">domain</code> and <code class=\"language-text\">range</code> applied. This prop should not be set manually.</p>\n<h3 id=\"style\"><a href=\"#style\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>style</h3>\n<p><code class=\"language-text\">type: object</code></p>\n<p>The <code class=\"language-text\">style</code> prop specifies the styles that will be applied to the <code class=\"language-text\">lineComponent</code>. This prop should be given as\nan object of SVG style attributes.</p>\n<h3 id=\"type\"><a href=\"#type\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>type</h3>\n<p><code class=\"language-text\">type: string</code></p>\n<p>The <code class=\"language-text\">type</code> is used to specify which event target a particular <code class=\"language-text\">VictoryBrushLine</code> belongs to. When <code class=\"language-text\">VictoryBrushLine</code> is used by <code class=\"language-text\">VictoryAxis</code> as its <code class=\"language-text\">axisComponent</code> or <code class=\"language-text\">gridComponent</code>, this prop will be set automatically to “axis” or “grid” as appropriate.</p>\n<h3 id=\"width\"><a href=\"#width\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>width</h3>\n<p><code class=\"language-text\">type: number</code></p>\n<p>The <code class=\"language-text\">width</code> prop specified the width of both the <code class=\"language-text\">brush</code> and <code class=\"language-text\">brushArea</code>. When <code class=\"language-text\">brushWidth</code> or <code class=\"language-text\">brushAreaWidth</code> are specified, this prop will not be used</p>\n<p><em>default:</em> <code class=\"language-text\">width={10}</code></p>","frontmatter":{"id":4,"scope":null,"title":"VictoryBrushLine"},"fields":{"slug":"/docs/victory-brush-line/"}}},"pathContext":{"slug":"/docs/victory-brush-line/"}}

/***/ })

});


